
export default {
  props: {
    parameters: {
      required: true
    },
    paramType: {
      required: true,
      type: String
    },
    title: {
      required: true,
      type: String
    },
    subPropsIndex: {
      required: false
    }
  },
  data () {
    return {
      active: true
    }
  },
  created () {
    this.active = true
  },
  methods: {
    fieldUpdated (index, value) {
      this.$emit('fieldUpdated', { index: index, value: value })
    },
    onModalCancel () {
      this.$emit('modalCanceled')
    },
    onModalConfirm () {
      this.$emit('modalConfirmed')
    }
  },
  components: {
    'form-fields': () => import('../form-fields/FormFields.vue')
  }
}



